<template>
    <div class="layout" v-if="basket.length !== 0">
        <p class="title">Оформление Заказа</p>

        <ProductsTable v-bind:data="basket" :is-editable="!isProcessing && !isProcessed"
                       @remove="removeFromBasket" @quantityChange="onQuantityChange">
        </ProductsTable>

        <div class="comment">
            <MDInput label="Комментарий" ref="comment">
            </MDInput>
        </div>

        <div class="bottom">
            <LoadingSpinner v-if="isProcessing && !isProcessed" class="loading-spinner"/>
            <p v-if="isProcessed">Оформлена</p>
            <MDRaisedButton text="Оформить" v-if="!isProcessing && !isProcessed" v-on:click="createOrder"></MDRaisedButton>

            <p class="total-price">Всего: <span>{{basketTotalPrice}}₴</span></p>
        </div>

    </div>

    <div v-else>
        <p v-if="basket.length === 0 && !isProcessed" class="empty">Накладная пуста</p>

        <p v-if="basket.length === 0 && isProcessed" class="empty">Накладная оформлена</p>
    </div>
</template>

<script>
    import { mapGetters, mapMutations } from 'vuex';
    import modules from "../store/all.modules.name"
    import mutations from "../store/all.mutations.type";

    import ProductsTable from "./components/ProductsTable";
    import LoadingSpinner from "./components/LoadingSpinner"
    import MDInput from "./components/MD/MDInput";
    import MDRaisedButton from "./components/MD/MDRaisedButton";

    import {CatalogAPI} from "../api/catalog"

    export default {
        name: "Ordering",

        components: {MDRaisedButton, MDInput, ProductsTable, LoadingSpinner},

        data: () => {
            return {
                isProcessing: false,
                isProcessed: false,
            }
        },

        methods: {
            ...mapMutations(modules.catalog, [
                mutations.catalog.REMOVE_FROM_BASKET,
                mutations.catalog.CLEAR,
            ]),

            onQuantityChange(payload) {
                let {item, newQuantity} = payload;
                item.boughtQuantity = newQuantity;
            },

            createOrder() {
                this.isProcessing = true;
                this.$refs.comment.isEditable = false;

                let products = [];
                this.basket.forEach((item) => {
                    products.push({
                        id: item.id,
                        boughtQuantity: item.boughtQuantity,
                    })
                });

               CatalogAPI.createOrder(this.$refs.comment.data, products)
                   .then(() => {
                       this.isProcessed = true;
                       this.isProcessing = false;
                       this.clear();
                   })
                   .catch(e => {
                       this.isProcessed = false;
                       this.isProcessing = false;
                       console.error(e);
                       alert("Произошла ошибка во время обработки накладной");
                   })
            },
        },

        computed: {
            ...mapGetters(modules.catalog, [
                'basket',
                'isBasketValid',
                'basketTotalPrice',
            ]),
        },
    }
</script>

