<template>
    <section class="md-input-container" v-bind:class="{'not-valid': !isValid}">
        <section class="md-input" v-bind:class="{'not-empty': !isEmpty}">
            <input type="text" v-if="!isTextArea"  @input="onInput" :disabled="!isEditable"/>
            <textarea v-else @input="onInput" rows="2" :disabled="!isEditable"/>

            <span class="bar"></span>
            <label>{{label}}</label>
        </section>
    </section>
</template>

<script>
    export default {
        name: "MDInput",

        props: {
            label: {
                type: String,
                required: true,
            },

            isTextArea: {
                type: Boolean,
                default: false,
            },
        },

        data: () => {
            return {
                isEmpty: true,
                isValid: true,
                isEditable: true,
                data: "",
            }
        },

        methods: {
            onInput(event) {
                let length = event.target.value.length;
                this.isEmpty = length === 0;
                this.data = event.target.value;
                this.$emit('input');
            }
        }
    }
</script>

<style lang="sass" scoped>
    .not-empty
        label
            top: -10px!important
            font-size: 14px!important

    .md-input-container
        background-color: #f2f2f2
        display: flex
        border-top-right-radius: 5px
        border-top-left-radius: 5px
        padding-top: 5px

    .not-valid

        input, textarea
            & ~ label
                color: red!important

            & ~ .bar
                &:before
                    width: 50%
                &:after
                    width: 50%

        .bar
            &:before, &:after
                background-color: red!important

    .md-input
        position: relative
        margin-top: 10px
        width: 100%

        input, textarea
            font-size: 18px
            padding: 10px 10px 10px 5px
            display: block
            border: none
            resize: none
            box-sizing: border-box
            width: 100%
            background-color: transparent

            &:focus
                outline: none

                & ~ label
                    top: -10px
                    font-size: 14px
                    color: #5264AE

            &:focus
                & ~ .bar
                    &:before
                        width: 50%
                    &:after
                        width: 50%

        label
            color: #999
            font-size: 18px
            font-weight: normal
            position: absolute
            pointer-events: none
            left: 5px
            top: 10px
            transition: 0.2s ease all
            -moz-transition: 0.2s ease all
            -webkit-transition: 0.2s ease all

        .bar
            position: relative
            display: block
            width: 100%

            &:before, &:after
                content: ''
                height: 2px
                width: 0
                bottom: 1px
                position: absolute
                background: #5264AE
                transition: 0.2s ease all
                -moz-transition: 0.2s ease all
                -webkit-transition: 0.2s ease all

            &:before
                left: 50%

            &:after
                right: 50%
</style>